import React  from 'react';
import { Container,  Card, Row, Col  } from 'react-bootstrap';
import { useHistory  } from "react-router-dom";
import { useUtils, useImage } from '../customHooks';

export default ()=> {
    const { history } = useHistory();
    const { copyableShow } = useUtils();
    const metaImage = useImage();

    const bodyBox = (comp)=> centrelBox(<Container fluid={true} className="p-0 m-0 pb-3 text-left clearfix border border-warning rounded" 
        style={{minHeight:'36rem'}}>
            {comp}
        </Container>)

    const centrelBox = (comp)=> <Container fluid={true} className="p-0 pl-3 pr-3">
        <Container fluid={true} className="p-0 pl-3 pr-3  d-none d-xl-block">
        <Container fluid={true} className="p-0 pl-3 pr-3">
        <Container fluid={true} className="p-0 pl-3 pr-3">
            <Container fluid={true} className="p-0 pl-3 pr-3">
                {comp}
            </Container>
        </Container>
        </Container>
        </Container>

        <Container fluid={true} className="p-0 d-xl-none">
            {comp}
        </Container>
    </Container>

    const headerBox = (comp)=> <Container fluid={true} className="p-0 text-left clearfix text-light header-bg" style={{minHeight:'8.2rem'}}>
            {centrelBox(comp)}
        </Container>
    
    const cardShow = (v, k)=> <Container key={k} className="p-3 m-0 float-left text-center" style={{ width: '25%', minWidth:'20rem'}}>
    <Card border={v.style}  className={`rounded-0 bg-${v.style} hover-shadow p-0 pl-1`} 
        style={{ width:'100%', minWidth:'18rem', height:'13rem' }}>
        <Card.Body className="bg-light p-3" onClick={ ()=> window.location.href = v.link.caption ? `#${v.link.link}` : '#'}>
            <Card.Title>{v.title}</Card.Title>
            <Card.Subtitle className="mb-2 text-secondary" style={{fontSize:'0.9rem'}}>{v.subtitle}</Card.Subtitle>
            <Card.Text style={{height:'5.2rem'}}>{v.description}</Card.Text>
        </Card.Body>
    </Card>
    </Container>

    const itemBox = (v, k)=> <Container key={k} className="p-1 m-0 float-left text-center" 
        style={{ width: '10%', minWidth:'10rem',height:'4rem' }}>
        <Card border="warning"  className={`rounded hover-shadow p-0`} 
            style={{ width:'100%', height:'100%' }}>
            <Card.Body className="p-0" onClick={ ()=> window.location.href = v.link.caption ? `#${v.link.link}` : '#'}>
                <Card.Title>{metaImage.showOrigin('postBox', {width:'2rem'}) }</Card.Title>
                <Card.Subtitle className="text-secondary" style={{fontSize:'0.9rem'}}>{copyableShow(v.address, 4)}</Card.Subtitle>
            </Card.Body>
        </Card>
    </Container>

    const itemIcon = (v, k)=> <Container key={k} className="p-1 m-0 float-left text-center" 
    style={{ width: '10%', minWidth:'8rem',height:'4rem' }}>
    <Card border="warning"  className={`rounded hover-shadow p-0`} 
        style={{ width:'100%', height:'100%' }}>
        <Card.Body className="p-0 text-secondary text-left" onClick={ ()=> window.location.href = v.link.caption ? `#${v.link.link}` : '#'}>
            <Row>
                <Col className="d-flex align-items-center pt-1" xs={4}>{metaImage.showOrigin('postBox', {style:{width:'2.8rem'}}) }</Col>
                <Col xs={8} className="d-flex align-items-center justify-content-left">{v.name.slice(0,12)}</Col>
            </Row>
        </Card.Body>
    </Card>
    </Container>
    
    const cardListShow = (cards)=> cards.map((v,k)=> cardShow(v, k));

  return { centrelBox, cardListShow, cardShow, headerBox, bodyBox, itemBox, itemIcon }
}