import React  from 'react';
import Web3  from 'web3';

export default ()=> {
  const web3 =  new Web3();

  const show = (addr, l)=> l?  addr.slice(0,l) + '...' + addr.slice(-1 * l) : addr;

  const copyText = (s)=> !navigator || !navigator.clipboard || !navigator.clipboard.writeText ? 
        '' : navigator.clipboard.writeText(s);

  const copyableShow = (addr,l)=> <span className="copyable" onClick={()=>copyText(addr)}>{show(addr, l)}</span>;

  return { show, copyableShow, web3utils:web3.utils }
} 