import React,  { useState, useEffect}  from 'react';
import { Container, Button, Form, Row, Col, Image } from 'react-bootstrap';
import useUNST from '../contracts/useUNST';
import { SettingStore } from '../stores';
import { Engine } from '../comm';

import { useUtils, usePostOffice, useImage } from '../customHooks';

export default (props)=> {
  const eng = new Engine();

  const { getUNSTInfo, createPostoffice, askPrice} = useUNST('UNST', 'https://rinkeby.infura.io/v3/3af2ebd23289490dbe29d3034e2887ce');
  const [ unstInfo, setUnstInfo ] = useState({});
  const [ formrec, setFormrec ] = useState({});
  const [ price, setPrice ] = useState(null);
  const metaImage = useImage();

  const [ po, setPo ] = useState([]);

  const { getPostOffices } = usePostOffice();
  const { copyableShow } = useUtils();

  const handleTextChange = async (e, id)=> {
    const rec = formrec;
    rec[id] = e.target.value;
    setFormrec(rec);
  //  setTm(new Date().getTime());
  }

  useEffect(()=>{
    setPo(SettingStore.getState().po);
    askPrice(SettingStore.getState().unst, v=>setPrice(v))
  }, []);


  const btnCreatePostoffice = ()=> <Button className="btn btn-info" onClick={()=>{
      eng.loadingOn();
      createPostoffice(SettingStore.getState().unst, formrec.name, v=>{
        SettingStore.dispatch({type:'addPo', data:v});
        eng.loadingOff();
      })
    }}>Funding a post office</Button>

  const formSection = <Container fluid={true} className="p-3">
  <Row>
    <Col lg={3} md={3} sm={3}  className="p-0 pl-2 pr-2 text-left">
        <Container className="d-flex align-items-center justify-content-center p-2" style={{height:'100%', width:'100%'}}>
          {metaImage.showOrigin('postOffice', {className:'', style:{width:'6rem'}}) }
        </Container>
    </Col>

    <Col lg={9} md={9} sm={9} className="alert-secondary rounded p-3">
      <Form.Group className="m-0 p-0">
        <Form.Label className="p-0 m-0 text-info">Minimum funding amount : {price} Ether</Form.Label>
      </Form.Group>

      <Form.Control type="text" defaultValue={formrec.name}
        placeholder="Enter a office name (Limit 32 characters)" className="p-1 m-0 mb-2 border"
        onChange={e=>handleTextChange(e, 'name')}/>

      {btnCreatePostoffice()}
    </Col>
  </Row>
  </Container> 

  return (
    <Container fluid={true} className="p-3" style={{minHeight:'32rem'}}>
      <Container fluid={true} className="p-3 bg-light rounded">
        {formSection}
      </Container>

      <Container fluid={true} className="p-0">
        {getPostOffices().map((v,k)=> {
            return <Container fluid={true} key={k} className="m-0 mt-3 p-3 alert-secondary rounded">
              <span className="mr-3">{metaImage.showOrigin('postOffice',
              {
                className: 'border border-secondary shadow-sm',
                style: {width:'3rem', borderRadius: '50%'}
                              
              }) }</span>
              <span className="mr-3">{copyableShow(v.address, 5)} </span>
              <span className="mr-3">{v.name} </span>
            </Container>;
          })}

        {getPostOffices().map((v,k)=> {
            return <Container fluid={true} key={k} className="m-0 mt-3 p-3 alert-secondary rounded">
              <span className="mr-3">{metaImage.showOrigin('postOffice', 
                {
                  className: 'border border-secondary shadow-sm',
                  style: {width:'3rem', borderRadius: '50%'}
                })}</span>
              <span className="mr-3">{copyableShow(v.address, 5)} </span>
              <span className="mr-3">{v.name} </span>
            </Container>;
          })
          }
      </Container>
    </Container>
  );
}