import React, {useEffect, useState} from 'react';
import { Container,  Image } from 'react-bootstrap';
import useUNST from '../contracts/useUNST';
import { SettingStore } from '../stores';
import { useUtils, usePostOffice, useUIComponents } from '../customHooks';
import Web3 from 'web3';

export default (props)=> {
//  const [ postOffices, setPostOffices ] = useState([]);
  const [ tm, setTm ] = useState(0);
  const { getUNSTInfo } = useUNST('UNST', 'https://rinkeby.infura.io/v3/3af2ebd23289490dbe29d3034e2887ce');
  const [ unstInfo, setUnstInfo ] = useState({});

  const { copyableShow, web3utils } = useUtils();
  const { getPostOffices } = usePostOffice();
  const { itemIcon } = useUIComponents()
  useEffect(()=>{
    (async()=>{
      getUNSTInfo(SettingStore.getState().unst, v=>{
        console.log(v);
        setUnstInfo(v);
      })
    })()


  }, [])
  return <Container fluid={true} className="p-3 border border-warning rounded bg-tranparent-box"> 
  <Container fluid={true} className="p-0 pt-1 clearfix text-left text-warning" style={{minHeight:'5rem'}}>
    <span className="float-left m-0 mr-2 p-1">
      <h5 className="text-warning text">{unstInfo.symbol}
        {!unstInfo || !unstInfo.name? '' : <span className="ml-1 p-1" style={{fontSize:'1.1rem'}}>({unstInfo.name})</span>}
      </h5>
    </span>
    {/*<span className="float-left m-0 mt-2 mr-2 p-1 bg-inverse border border-success rounded">Pool: {unstInfo.poolSize}</span>*/}
    <span className="float-left m-0 mr-2 p-1 bg-inverse border border-success  rounded">UNST/WEI Rate  : 
      {unstInfo.rate}</span>
    <span className="float-left m-0 mr-2 p-1 bg-inverse border border-success rounded">
      Pool: {Web3.utils.fromWei(!unstInfo || !unstInfo.ethBalance? '0' : unstInfo.ethBalance ,'ether')} ETH</span>
    <Container className="clearfix"></Container>

    {getPostOffices().map((v,k)=> itemIcon(v,k))}
    
    <Container className="clearfix"></Container>

  </Container>
  <Container className="clearfix"></Container>

</Container>
}
